/* =============================================================================
   Modern Startup Design System - IMA Systems Group
   Color Palette: Black, White, and Blue Accent
   Inspired by: Stripe, Figma, Vercel
   ============================================================================= */

/* CSS Variables - ENHANCED CRISP PALETTE */
:root {
  /* Colors - Premium Refined Palette */
  --color-deep-ink: #0a0d1a;
  --color-midnight: #0f1427;
  --color-slate: #1a1f35;
  --color-cyan-bright: #00e5ff;
  --color-ocean: #0099dd;
  --color-emerald: #00d47f;
  --color-slate-gray: #4a5568;
  --color-white: #ffffff;
  --color-light-gray: #f0f2f5;
  --color-medium-gray: #b0b5bc;
  --color-dark-gray: #6b7280;

  /* Text Colors - Enhanced Contrast */
  --text-primary: #ffffff;           /* Crisp white */
  --text-secondary: #d1d5db;         /* Brighter secondary */
  --text-tertiary: #9ca3af;          /* Readable tertiary */
  --bg-primary: #0a0d1a;
  --bg-secondary: #0f1427;
  --bg-tertiary: #1a1f35;
  --border: #2d3748;
  --accent: #00e5ff;
  --accent-secondary: #0099dd;
  --accent-tertiary: #00d47f;
  
  /* Glow Effects - Crisper */
  --glow-cyan: 0 0 24px rgba(0, 229, 255, 0.35);
  --glow-blue: 0 0 24px rgba(0, 153, 221, 0.30);
  --glow-emerald: 0 0 24px rgba(0, 212, 127, 0.25);

  /* Typography - Premium */
  --font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  --font-serif: 'Syne', 'Georgia', serif;
  --font-mono: 'IBM Plex Mono', 'Courier New', monospace;
  --font-size-xs: 12px;
  --font-size-sm: 14px;
  --font-size-base: 16px;
  --font-size-lg: 18px;
  --font-size-xl: 20px;
  --font-size-2xl: 24px;
  --font-size-3xl: 32px;
  --font-size-4xl: 48px;
  --font-size-5xl: 56px;

  /* Spacing - 8px base unit */
  --spacing-xs: 4px;
  --spacing-sm: 8px;
  --spacing-md: 16px;
  --spacing-lg: 24px;
  --spacing-xl: 32px;
  --spacing-2xl: 48px;
  --spacing-3xl: 64px;
  --spacing-4xl: 80px;

  /* Effects - Refined Shadows */
  --shadow-sm: 0 1px 3px rgba(0, 0, 0, 0.12);
  --shadow-md: 0 4px 12px rgba(0, 0, 0, 0.15);
  --shadow-lg: 0 12px 32px rgba(0, 0, 0, 0.18);
  --shadow-xl: 0 20px 48px rgba(0, 0, 0, 0.20);
  --transition-base: 200ms ease-in-out;
  --transition-slow: 300ms ease-in-out;

  /* Border Radius */
  --radius-sm: 4px;
  --radius-md: 8px;
  --radius-lg: 12px;
}

/* Reset & Base Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  min-height: 100vh;
  color: var(--text-primary);
  background: var(--bg-primary);
  font-family: var(--font-family);
  font-size: var(--font-size-base);
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

main {
  width: 100%;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Typography */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-serif);
  font-weight: 700;
  line-height: 1.3;
  color: var(--text-primary);
  letter-spacing: -0.02em;
}

h1 {
  font-size: 56px;
  margin-bottom: var(--spacing-md);
}

h2 {
  font-size: var(--font-size-3xl);
  margin-bottom: var(--spacing-lg);
}

h3 {
  font-size: var(--font-size-xl);
  margin-bottom: var(--spacing-md);
}

p {
  margin-bottom: var(--spacing-md);
  color: var(--text-secondary);
  line-height: 1.8;
}

a {
  color: var(--accent);
  text-decoration: none;
  transition: color var(--transition-base);
}

a:hover {
  color: var(--accent-dark);
}

/* Container */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 var(--spacing-lg);
}

/* Header / Navigation */
.header {
  background: var(--bg-primary);
  border-bottom: 1px solid var(--border);
  padding: var(--spacing-md) 0;
  position: sticky;
  top: 0;
  z-index: 100;
  backdrop-filter: blur(10px);
  background: rgba(255, 255, 255, 0.95);
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 var(--spacing-lg);
}

.header h1 {
  font-size: var(--font-size-2xl);
  margin-bottom: 0;
  font-weight: 700;
}

.header .tagline {
  font-size: var(--font-size-sm);
  color: var(--text-tertiary);
  margin: 0;
  margin-top: 4px;
}

.nav {
  display: flex;
  gap: var(--spacing-xl);
  align-items: center;
}

.nav a {
  font-size: var(--font-size-sm);
  color: var(--text-primary);
  transition: color var(--transition-base);
  font-weight: 500;
}

.nav a:hover {
  color: var(--accent);
}

.nav-active {
  color: var(--accent) !important;
  font-weight: 600;
  border-bottom: 2px solid var(--accent);
  padding-bottom: 2px;
}

.btn-admin {
  background: var(--accent);
  color: white !important;
  padding: var(--spacing-sm) var(--spacing-lg) !important;
  border-radius: var(--radius-md);
  font-weight: 600;
  font-size: var(--font-size-sm);
  transition: all var(--transition-base);
}

.btn-admin:hover {
  background: var(--accent-dark);
  color: white !important;
}

/* Hero Section */
.hero {
  padding: var(--spacing-4xl) var(--spacing-lg);
  text-align: center;
  background: linear-gradient(135deg, var(--bg-primary) 0%, var(--bg-secondary) 100%);
  border-bottom: 1px solid var(--border);
}

.hero h2 {
  font-size: var(--font-size-4xl);
  margin-bottom: var(--spacing-lg);
  font-weight: 700;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.hero p {
  font-size: var(--font-size-lg);
  color: var(--text-secondary);
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: var(--spacing-xl);
}

/* Buttons */
.cta-button {
  display: inline-block;
  background: var(--accent);
  color: white;
  padding: var(--spacing-md) var(--spacing-xl);
  border-radius: var(--radius-md);
  font-weight: 600;
  font-size: var(--font-size-sm);
  transition: all var(--transition-base);
  cursor: pointer;
  border: none;
  text-align: center;
}

.cta-button:hover {
  background: var(--accent-dark);
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.btn-secondary {
  background: var(--bg-secondary);
  color: var(--text-primary);
  border: 1px solid var(--border);
}

.btn-secondary:hover {
  background: var(--bg-tertiary);
  border-color: var(--text-primary);
}

/* Link Buttons */
.view-more {
  display: inline-flex;
  align-items: center;
  gap: var(--spacing-sm);
  padding: var(--spacing-sm) var(--spacing-md);
  border-radius: var(--radius-md);
  font-weight: 600;
  font-size: var(--font-size-sm);
  transition: all var(--transition-base);
  border: 1px solid var(--border);
  background: var(--bg-primary);
  color: var(--accent) !important;
}

.view-more:hover {
  background: var(--bg-secondary);
  border-color: var(--accent);
  color: var(--accent-dark) !important;
  transform: translateX(2px);
}

/* Sections */
section {
  width: 100%;
}

/* Footer */
.footer,
footer {
  background: var(--bg-secondary);
  border-top: 1px solid var(--border);
  padding: var(--spacing-2xl) var(--spacing-lg);
  text-align: center;
  color: var(--text-secondary);
  font-size: var(--font-size-sm);
  margin-top: auto;
}

/* Forms */
input,
textarea,
select {
  width: 100%;
  padding: var(--spacing-md);
  font-size: var(--font-size-base);
  border: 1px solid var(--border);
  border-radius: var(--radius-md);
  font-family: var(--font-family);
  transition: border-color var(--transition-base), box-shadow var(--transition-base);
  background: var(--bg-primary);
  color: var(--text-primary);
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
  border-color: var(--accent);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

label {
  display: block;
  margin-bottom: var(--spacing-sm);
  font-weight: 600;
  font-size: var(--font-size-sm);
  color: var(--text-primary);
}

/* Admin Dashboard */
.admin-layout {
  display: grid;
  grid-template-columns: 250px 1fr;
  min-height: 100vh;
}

.sidebar {
  background: var(--bg-secondary);
  border-right: 1px solid var(--border);
  padding: var(--spacing-xl);
  position: sticky;
  top: 0;
  height: 100vh;
  overflow-y: auto;
}

.sidebar h2 {
  font-size: var(--font-size-xl);
  margin-bottom: var(--spacing-lg);
}

.sidebar-menu {
  list-style: none;
}

.sidebar-menu li {
  margin-bottom: var(--spacing-md);
}

.sidebar-menu a {
  display: block;
  padding: var(--spacing-md);
  border-radius: var(--radius-md);
  color: var(--text-primary);
  transition: all var(--transition-base);
}

.sidebar-menu a:hover,
.sidebar-menu a.active {
  background: var(--accent);
  color: white;
}

.admin-content {
  padding: var(--spacing-2xl);
}

.admin-header {
  margin-bottom: var(--spacing-2xl);
}

.admin-header h1 {
  font-size: var(--font-size-3xl);
  margin-bottom: var(--spacing-sm);
}

.admin-container {
  background: var(--bg-secondary);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  padding: var(--spacing-2xl);
  box-shadow: var(--shadow-md);
}

.admin-sidebar-item a {
  transition: all var(--transition-base);
}

.admin-sidebar-item a:hover {
  background: rgba(0, 229, 255, 0.08);
  color: var(--text-primary);
  box-shadow: inset 0 0 0 1px rgba(0, 229, 255, 0.2);
}

/* Login Page */
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: var(--spacing-lg);
  background: linear-gradient(135deg, var(--bg-primary) 0%, var(--bg-secondary) 100%);
}

.login-card {
  background: var(--bg-primary);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  padding: var(--spacing-3xl);
  max-width: 400px;
  width: 100%;
  box-shadow: var(--shadow-lg);
}

.login-card h1 {
  font-size: var(--font-size-2xl);
  margin-bottom: var(--spacing-xl);
  text-align: center;
}

.login-card .form-group {
  margin-bottom: var(--spacing-lg);
}

.login-card .cta-button {
  width: 100%;
  margin-top: var(--spacing-lg);
}

/* Tables */
table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: var(--spacing-xl);
}

thead {
  background: var(--bg-secondary);
  border-bottom: 2px solid var(--border);
}

th {
  padding: var(--spacing-md);
  text-align: left;
  font-weight: 600;
  color: var(--text-primary);
  font-size: var(--font-size-sm);
}

td {
  padding: var(--spacing-md);
  border-bottom: 1px solid var(--border);
  color: var(--text-secondary);
}

tr:hover {
  background: var(--bg-secondary);
}

/* Cards */
.card {
  background: var(--bg-primary);
  border: 1px solid var(--border);
  border-radius: var(--radius-md);
  padding: var(--spacing-lg);
  transition: all var(--transition-base);
}

.card:hover {
  box-shadow: var(--shadow-md);
}

/* Patents Page Styles */
.patents-section {
  padding: var(--spacing-3xl) 0;
  flex: 1;
}

.patents-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: var(--spacing-xl);
  margin-top: var(--spacing-xl);
}

.patent-card {
  background: var(--bg-primary);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  padding: var(--spacing-xl);
  transition: all var(--transition-slow);
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.patent-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, var(--accent), var(--accent-light));
}

.patent-card:hover {
  box-shadow: var(--shadow-lg);
  transform: translateY(-4px);
  border-color: var(--accent);
}

.patent-card-badge {
  display: inline-block;
  background: var(--accent);
  color: white;
  padding: var(--spacing-xs) var(--spacing-md);
  border-radius: var(--radius-sm);
  font-size: var(--font-size-xs);
  font-weight: 700;
  margin-bottom: var(--spacing-md);
  width: fit-content;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.patent-card h3 {
  margin-top: var(--spacing-sm);
  color: var(--text-primary);
}

.patent-card p {
  flex-grow: 1;
  color: var(--text-secondary);
  margin-bottom: var(--spacing-lg);
}

.patent-meta {
  display: flex;
  gap: var(--spacing-md);
  margin: var(--spacing-lg) 0;
  padding: var(--spacing-md) 0;
  border-top: 1px solid var(--border);
  border-bottom: 1px solid var(--border);
  font-size: var(--font-size-sm);
  color: var(--text-tertiary);
}

.video-container {
  width: 100%;
  margin: var(--spacing-lg) 0;
  border-radius: var(--radius-md);
  overflow: hidden;
  background: var(--bg-secondary);
  aspect-ratio: 16 / 9;
}

.video-container iframe {
  width: 100%;
  height: 100%;
  border: none;
}

.empty-state {
  text-align: center;
  padding: var(--spacing-4xl) var(--spacing-xl);
  background: var(--bg-secondary);
  border-radius: var(--radius-lg);
  border: 2px dashed var(--border);
}

.empty-state h3 {
  color: var(--text-primary);
  margin-bottom: var(--spacing-md);
}

.empty-state p {
  color: var(--text-secondary);
  margin-bottom: var(--spacing-xl);
}

.loading {
  text-align: center;
  padding: var(--spacing-3xl);
  font-size: var(--font-size-lg);
  color: var(--text-secondary);
}

/* Responsive */
@media (max-width: 768px) {
  h1 {
    font-size: var(--font-size-2xl);
  }

  h2 {
    font-size: var(--font-size-2xl);
  }

  .hero {
    padding: var(--spacing-2xl) var(--spacing-lg);
  }

  .hero h2 {
    font-size: var(--font-size-2xl);
  }

  .nav {
    gap: var(--spacing-md);
    flex-wrap: wrap;
  }

  .admin-layout {
    grid-template-columns: 1fr;
  }

  .sidebar {
    position: relative;
    height: auto;
    display: none;
  }

  .login-card {
    padding: var(--spacing-xl);
  }

  table {
    font-size: var(--font-size-sm);
  }

  th,
  td {
    padding: var(--spacing-sm);
  }

  .patents-grid {
    grid-template-columns: 1fr;
  }

  .patent-card {
    padding: var(--spacing-lg);
  }
}

@media (max-width: 480px) {
  .container {
    padding: 0 var(--spacing-md);
  }

  h1 {
    font-size: var(--font-size-xl);
  }

  h2 {
    font-size: var(--font-size-xl);
  }

  .hero h2 {
    font-size: var(--font-size-xl);
  }

  .hero p {
    font-size: var(--font-size-base);
  }

  .header h1 {
    font-size: var(--font-size-xl);
  }

  .nav {
    font-size: var(--font-size-sm);
  }
}
/* =============================================================================
   INTERACTIVE MICRO-INTERACTIONS & ANIMATIONS
   Enhanced user experience with subtle transitions
   ============================================================================= */

/* Button & Link Interactions */
button,
.btn-admin,
a[role="button"],
.btn-primary,
.btn-secondary {
  transition: all var(--transition-base);
  position: relative;
  overflow: hidden;
}

button:hover,
.btn-admin:hover,
a[role="button"]:hover,
.btn-primary:hover,
.btn-secondary:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(0, 229, 255, 0.2);
}

button:active,
.btn-admin:active,
a[role="button"]:active,
.btn-primary:active,
.btn-secondary:active {
  transform: translateY(0);
  box-shadow: 0 4px 12px rgba(0, 229, 255, 0.15);
}

/* Card Hover Effects */
.patent-card,
.card,
.feature-card,
.section-card {
  transition: all var(--transition-base);
  transform: translateZ(0);
}

.patent-card:hover,
.card:hover,
.feature-card:hover,
.section-card:hover {
  transform: translateY(-8px) translateZ(0);
  box-shadow: 0 16px 40px rgba(0, 229, 255, 0.1);
  border-color: rgba(0, 229, 255, 0.3);
}

/* Link Hover Effects */
a:not(.nav-active):not([role="button"]) {
  transition: color var(--transition-base), text-decoration var(--transition-base);
}

a:not(.nav-active):not([role="button"]):hover {
  color: var(--accent);
  text-decoration-color: var(--accent);
}

/* Form Input Interactions */
input:focus,
textarea:focus,
select:focus {
  outline: none;
  border-color: var(--accent);
  box-shadow: 0 0 0 3px rgba(0, 229, 255, 0.1);
  transition: all var(--transition-base);
}

input::placeholder,
textarea::placeholder {
  transition: color var(--transition-base);
}

input:focus::placeholder,
textarea:focus::placeholder {
  color: transparent;
}

/* Smooth Page Transitions */
@keyframes pageEnter {
  from {
    opacity: 0;
    transform: translateY(12px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

main {
  animation: pageEnter 0.4s ease-out;
}

/* Section Reveal Animation */
@keyframes sectionReveal {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

section {
  animation: sectionReveal 0.5s ease-out backwards;
}

section:nth-child(1) {
  animation-delay: 0s;
}

section:nth-child(2) {
  animation-delay: 0.1s;
}

section:nth-child(3) {
  animation-delay: 0.2s;
}

section:nth-child(4) {
  animation-delay: 0.3s;
}

section:nth-child(5) {
  animation-delay: 0.4s;
}

/* Smooth Scrolling */
html {
  scroll-behavior: smooth;
}

/* Focus Visible for Accessibility */
:focus-visible {
  outline: 2px solid var(--accent);
  outline-offset: 2px;
}

/* Reduced Motion Accessibility */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}